import { useSelector } from "react-redux";

import styled from "styled-components";

import {
  State,
  GameState,
  TypeEffect,
  PlayerListType,
} from "../business/types";

import { PlayerStatus } from "./PlayerStatus";
import { PlayersStatusList } from "./PlayersStatusList";

const Status = styled.div`
  width: 200px;
  min-height: 18px;
  width: 250px;
  height: 60px;
  border: 1px solid lightgray;
  background-color: #fff2d9;
  text-align: center;
  font-size: 20px;
  color: #5f5757;
  padding-top: 15px;
  box-sizing: border-box;
`;

export const StatusList = () => {
  const { dice, playerList, gameState, doEffect, gameResult } = useSelector(
    (state: State) => ({
      ...state,
    })
  );
  return (
    <>
      <Status>{getTextStatus(gameState, doEffect, dice, gameResult)}</Status>
      <PlayerStatus />
      {/*    <PlayersStatusList /> */}
    </>
  );
};

const getTextStatus = (
  gameState: GameState,
  doEffect: TypeEffect,
  dice: number,
  gameResult: string
) => {
  switch (gameState.type) {
    case "gameStarted.trownDice":
      return "–±—Ä–æ—Å–∏—Ç—å –∫—É–±–∏–∫";
    case "gameStarted.playerMove":
      return "—Å–¥–µ–ª–∞—Ç—å —Ö–æ–¥";
    case "gameStarted.takeCard":
      return "–æ—Ç–∫—Ä—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É";
    case "gameStarted.applyCard":
      return "–ø—Ä–∏–º–µ–Ω–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É";
    case "gameStarted.interactEnemyCard":
      switch (doEffect?.type) {
        case "!openEnemyCard": {
          return "–æ—Ç–∫—Ä—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É";
        }
        case "!throwBattleDice": {
          return "p–µ–∂–∏–º –±–æ—è: –±—Ä–æ—Å–∏—Ç—å –∫—É–±–∏–∫";
        }
        case "!getBattleResult": {
          switch (dice) {
            case 1:
            case 2: {
              return `–≤—ã–ø–∞–ª–æ ${dice}: –∏–≥—Ä–æ–∫ —Å–ø–∞—Å–∞–µ—Ç—Å—è –±–µ–≥—Å—Ç–≤–æ–º `;
            }
            case 3: {
              return `–≤—ã–ø–∞–ª–æ ${dice}: –∏–≥—Ä–æ–∫ —Ç–µ—Ä—è–µ—Ç 1 –∑–¥–æ—Ä–æ–≤—å–µ`;
            }
            case 4: {
              return `–≤—ã–ø–∞–ª–æ ${dice}: –≤—Ä–∞–≥ –ø–æ–±–µ–∂–¥–µ–Ω`;
            }
            default:
              return " ";
          }
        }
        default:
          return " ";
      }

    case "endGame":
      return gameResult;
    default:
      return " ";
  }
};
